<template>
	<div class="contain clear"  :style="{height:height+'px'}">
		<header class="header">
			<p></p>
		</header>
		<section class="section">
			<article class="news1 news clear">
				<p class="list1 list clear">
					<span></span>
					<input type="text" placeholder="" v-model="bankData.accountName"/>
				</p>
				<p class="list2 list clear">
					<span></span>
					<input type="text" placeholder="" v-model="bankData.idNumber" />
				</p>
				<p class="list1 list clear">
					<span></span>
					<select v-model="bankData.bank_value">
						<option v-for="key in bankList" :value="key.id+','+key.name">{{key.name}}</option>
					</select>
				</p>
				<p class="list4 list clear">
					<span></span>
					<input type="text" v-model="bankData.account_number" placeholder="" onkeyup="value=value.replace(/[^\d]/g,'')" />
				</p>
			</article>
			<article class="news4 news clear">
				<p class="list1 list clear">
					<span id="phoneTxt"></span>
					<input type="text" v-model="bankData.bank_reserved_phone" placeholder="" maxlength="11" onkeyup="value=value.replace(/[^\d]/g,'')"  />
				</p>
				<p class="list1 list clear">
					<span></span>
					<input type="text" v-model="bankData.verifyCode" id="authCode" placeholder="" onkeyup="value=value.replace(/[^\d]/g,'')"  />
					<input id="getCode" @click="changeCode()" :class="{ changeCode: isSetCode }" :disabled="isSetCode"  type="button" v-model="setCode" />
				</p>
			</article>
		</section>
		<footer class="footer">
			<p class="btn" @click="submit()"></p>
		</footer>
	</div>
</template>
<script type="text/javascript">
let bankList = [						//
                {id:"104",name:""},
                {id:"102",name:""},
                {id:"105",name:""},
                {id:"103",name:""},
                {id:"303",name:""},
                {id:"302",name:""},
                {id:"307",name:""},
                {id:"100",name:""},
                {id:"301",name:""},
                {id:"309",name:""},
                {id:"306",name:""},
                {id:"316",name:""},
                {id:"310",name:""},
                {id:"401",name:""},
                {id:"424",name:""},
				]
	let bankData = {
			bank_card_type : "10" ,
			accountName : "" ,
			idNumber : "" ,
			bank_value : "" ,
			account_number : "" ,
			bank_reserved_phone : "" ,
			verifyCode : "",
			txSNBinding : ""
		}
	export default{
		data(){
			return{
				height:"",
				'setCode':"" ,
				'i':"" ,
				'isSetCode':false ,
				bankList ,
				bankData ,
				bank_value : "" ,
			}
		},
		methods: {
			init(){

			},
			//
            changeCode(){
				let _this = this ;
				//
				if(_this.bankData.accountName == "" || _this.bankData.accountName == null){
					_this.$toast("")
					return false ;
				}
				//
				if(!_this.$fn.checkIdNum(_this.bankData.idNumber)){
					_this.$toast('')
					return false ;
                }
				if(_this.bankData.bank_value == "" || _this.bankData.bank_value == null){
					_this.$toast("")
					return false ;
				}
				if(!_this.$fn.checkModile(_this.bankData.bank_reserved_phone)){
					_this.$toast('')
					return false ;
                }
				_this.getCode() ;   //
				_this.i = 60;
				var timer = setInterval(function() {
					_this.setCode = "" + _this.i + "s" ;
					_this.isSetCode = true ;
					_this.i = _this.i - 1;
					if(_this.i < 0) {
						clearInterval(timer);
						_this.setCode = "" ;
						_this.isSetCode = false ;
					} 
				}, 1000);
			},
			//
            getCode(){
				let _this = this ;
				let params = _this.bankData ;
				_this.$post(_this.$API.GET_MOBILE_CODE, params)
				.then((result) => {
					console.log("",result)
					if(result.result == "success"){
						_this.$toast("")
						_this.bankData.txSNBinding = result.data.txSNBinding ;
					}else{
						_this.$toast(result.msg)
						this.i = 0 ;
					}
				})
			},
			submit(){
				let _this = this ;
				if(_this.bankData.verifyCode == "" || _this.bankData.verifyCode == null){
					_this.$toast("")
					return false ;
				}
				let params = _this.bankData ;
				_this.$post(_this.$API.GET_BANK_CARD_SUBMIT, params)
				.then((result) => {
					console.log("",result)
					if(result.result == "success"){
						_this.$toast("")
						_this.$router.push({
							path:"/bankCards",	//
						}) ;
					}else{
						_this.$toast(result.msg)
						
					}
				})
			}
		},
		created () {
			this.height = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight; //
			this.init() ;
		},
	}
</script>
<style lang="scss" scoped type="text/css">
   @import "./index.scss";
</style>


