<template>
 			<div class="contain" :style="{height:height+'px'}">
			<header class="header">
				<p class="list1 list clear">
					
				</p>
				<p class="list2 list clear">
					<span class="predictPrice">{{predictPrice}}</span><span></span>
				</p>
				<p class="list3 list clear">
					
				</p>
			</header>
			<section class="section">
				<article class="news1 news">
					<p class="list1 list clear" @click="baseInfo()">
						<b></b>
						<span></span>
						<a>
							<i v-if="!isBaseInfo"></i>
						</a>
					</p>
					<p class="list2 list clear" @click="shopInfo()" style="border: 0;">
						<b></b>
						<span></span>
						<a>
							<i v-if="!isShopInfo"></i>
						</a>
					</p>
				</article>
				<article class="news2 news">
					<p class="list1 list clear">
						<b></b>
						<span></span>
					</p>
					<p class="list2 list clear">
						<i></i>
						<input type="text" id="amount" v-model="hopePrice" onkeyup="value=value.replace(/[^\d.]/g,'')" placeholder="(5~100)" />
						<span></span>
						
					</p>
					<p id="num" style="color: red; text-align: center;"></p>
					<p class="list3 list clear" style="border: 0;">
						<span></span>
						<select id="instament" v-model="operateProductId">
							<option v-for = "key in products" :value="key.id">{{key.productName}}</option>
						</select>
					</p>
				</article>
			</section>
			<div id="protocol" class="clear">
				<p class="p1">
					<input id="credit" type="checkbox" v-model="isChecked" @click="check()"/>
					<span></span>
					<font @click="credit = true"></font>
					<span style="width: 2px;"></span>
				</p>
				<p class="p2">
					<span></span>
					<font @click="informationAcquisition = true"></font>
					<span></span>
				</p>
			</div>
			<aside class="aside">
				<button class="btn" id="btn" @click="submit()" ></button>
			</aside>
      <modelShowCredit v-if="credit" @isShow="isShowCredit"  :name = "name" :idCard = "idCard" />
      <modelShowInformationAcquisition v-if="informationAcquisition" @isShow="isShowCredit" />
		</div>

</template>

<script>
import credit from '../../components/certificate/credit/index' ;	  //
import informationAcquisition from '../../components/certificate/informationAcquisition/index' ;	  //
export default {
  components:{
    "modelShowCredit" : credit ,
    "modelShowInformationAcquisition" : informationAcquisition
  },
  data() {
    return {
    	height:"" ,
        credit:false ,
	    informationAcquisition :false ,
	    isChecked : true ,
        isBaseInfo : true ,
		isShopInfo : true ,
		idCard : "" ,
		name : "" ,
		predictPrice : "100000" , //
		hopePrice : "" ,
		products : [] ,//
		operateProductId : "" , //
		flag : "" ,

    };
  },
  methods: {
		//
		init(){
			this.flag = this.$route.query.flag
			this.protocolInfo() ;
			this.loanInfo() ;
		},
		//
		loanInfo(){
			let _this = this ;
			_this.$fetch(_this.$API.GET_LOAN_MSG)
			.then((result) => {
				console.log("",result)
				if(result.result == "success"){
					_this.isBaseInfo = result.data.basicStatus;	//
					_this.isShopInfo = result.data.shopStatus ;	//
					let predictPrice = result.data.predictPrice ;//
					if(predictPrice==null||predictPrice<100000){
						_this.predictPrice = 100000 ;						
					}else{
						_this.predictPrice = predictPrice ;
					}
					let applyFlag = result.data.applyFlag;
					let isOld = result.data.isOld ; //
					let loanFlag = _this.$route.query.loanFlag	//
					_this.products = result.data.products ;	//
					//_this.operateProductId = result.data.operateProductId ;	//
					
					if(applyFlag == true) {
						//
						if(isOld == true&&loanFlag=='1') {
							_this.$router.push({
								path:"/loanSuccess",	//
							}) ;
						}
					} else {
							let applyStatus = result.data.applyStatus; //
							//flag
							if(_this.flag != null && _this.flag == 'true' && applyStatus == 3) {
								let repaymentMode = result.data.repaymentMode;
								_this.hopePrice = result.data.hopePrice/10000;
								_this.repaymentName = result.data.repaymentName;
								return;
							}
							_this.$router.push({
								path:"/loanSuccess",	//
							}) ;
						}
				}else{
					_this.$toast(result.msg)
				}
			})
		},
		//
		protocolInfo(){
			let _this = this ;
				_this.$fetch(_this.$API.GET_USER_MSG)
				.then((result) => {
					if(result.result == "success"){
						_this.idCard = result.data.idCard ;
						_this.name = result.data.name ;
					}else{
						_this.$toast(result.msg)
					}
				})
		},
		isShowCredit(data){
			this.credit = data ;
			this.informationAcquisition = data ;
		},
		//
		baseInfo(val){
			this.$router.push({
				path:"/basicInfo",
				query: { 
					"flag": this.flag ,
					"direction": true 
					}	
			}) ;
		},
		//
		shopInfo(val){
			this.$router.push({
				path:"/shopInfo",
				query: { 
					"flag": this.flag ,
					"direction": true 
				}	
			}) ;
		},
    submit(){
		let _this = this ;
		
		if(!_this.isChecked){
			_this.$toast("") ;
			return false ;
		}
		if(!_this.isBaseInfo){
			_this.$toast("") ;
			return false ;
		}
		if(!_this.isShopInfo){
			_this.$toast("") ;
			return false ;
		}
		if(_this.hopePrice == '' || _this.hopePrice < 5 || _this.hopePrice > 100){
			_this.$toast("") ;
			return false ;
		}
		let params={
			hopePrice : _this.hopePrice * 10000 ,	//
			operateProductId : _this.operateProductId , //
			predictPrice : _this.predictPrice 
		}
		_this.$post(_this.$API.PST_LOAN_SUBMIT,params)
		.then((result) => {
			if(result.result == "success"){
				_this.$router.push({
					path:"/loanSuccess",	//
				}) ;
			}else{
				_this.$toast(result.msg)
			}
		})
    }
  },
  created () {
		this.height = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight; //
		this.init() ;
  }

};
</script>
<style lang="scss" scoped>
   @import "./index.scss";
</style>



