<template>
    <div class="clear">
        <div class="modelWrap">
			<div class="wrap"></div>
			<div class="box">
				<div class="modelHeader">
					<h3></h3>
					<span @click="close()">x</span>
				</div>
				<div class="modelBody">
					<p class="textList"></p>
					<p>1.</p>
					<p class="textList"></p>
					<p>2.</p>
					<p class="textList"></p>
					<p>3.</p>
					<p class="textList"></p>
					<p>4.</p>
					<p class="textList">4.1</p>
					<p class="textList">4.2</p>
					<p>5.</p>
					<p class="textList">5.1</p>
					<p class="textList">5.2</p>
					<p class="textList">5.3</p>
					<p class="textList">5.4</p>
					<p class="textList">5.5</p>
					<p>6.</p>
					<p class="textList"></p>
					<p>7.</p>
					<p class="textList"></p>
					<p>8.</p>
					<p class="textList"></p>
					<p>9.</p>
					<p class="textList"></p>
					<p>10.</p>
					<p class="textList">7</p>
					<p>11.</p>
                    <p class="textList"></p>
					<p>12.</p>
					<p class="textList"></p>
					<p>13.</p>
					<p class="textList">13.1</p>
					<p class="textList">13.2</p>
				</div>
				<div class="modelFooter">
					<button @click="close()"></button>
				</div>
					
			</div>
		</div>
    </div>
</template>
<script>

export default {
    data(){
        return{
			isShow:false,
        }
	},
    methods:{
        close(){
           this.$emit( "isShow",false) ;
        },
        init(){
			
		},
    },
    created(){
        this.init() ;
    }
}
</script>
<style lang="scss" scoped type="text/css">
   .modelWrap {
    .wrap {
        position: fixed;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background: #000000;
        z-index: 11111;
        opacity: 0.7;
    }
    .box {
        position: absolute;
        top: 100px;
        left: 5%;
        padding: 0 2.5%;
        width: 85%;
        border-radius: 5PX;
        opacity: 1;
        z-index: 99999;
        background: #ffffff;
        margin-bottom: 100px;
        .modelHeader {
			position: relative;
            text-align: center;
            padding: 12PX 24PX;
            border-bottom: 2PX solid #e5e5e5;
            span {
				position: absolute ;
				right:10PX;
				top:12PX ;
				width: 20PX ;
				height: 20PX ;
				line-height: 20PX;
                font-size: 20PX;
                color: #bbb;
			}
			 h3 {
                text-align: center;
                color: #000000;
                font-size: 32px;
            }
        }
        .modelBody {
            padding: 20PX 0;
            border-bottom: 2PX solid #e5e5e5;
           
            p {
                font-size: 28px;
                color: #000333;
                padding: 8px 0;
			}
			.textList{
				text-indent:50px ;
			}
        }
        .modelFooter {
            padding: 10PX 24PX;
            text-align: right;
            button {
                color: #ffffff;
                background-color: #337ab7;
                border-color: #2e6da4;
                padding: 4PX 8PX;
                border-radius: 5PX;
            }
        }
    }
}
</style>




