<template>
	<div id="contain">
		<header id="header">
			<span :class="['nav',item ==100 ?'navItem':'']" @click="getData(100,100)"></span>
			<span :class="['nav',item ==0 ?'navItem':'']" @click="getData(1,0)"></span>
			<span :class="['nav',item ==1 ?'navItem':'']" @click="getData(1,1)"></span>
			<span :class="['nav',item ==3 ?'navItem':'']" @click="getData(1,3)"></span>
			<span :class="['nav',item ==4 ?'navItem':'']" @click="getData(1,4)"></span>
		</header>
		<section id="section">
			<article class="news1 news" v-for="key in loanList" @click="details(key.saasOrderId,key.refundStatus)">
				<p class="list0 list clear" v-if="key.refundStatus == 0">
					<span class="type0"></span>
					<font style="color:#ff5001;" v-if="key.surplusDay">{{key.surplusDay}}</font>
				</p>
				<p class="list0 list clear" v-if="key.refundStatus == 1">
					<span class="type1"></span>
					<font style="color:#ff5001;" v-if="key.realyRefundTime">{{key.realyRefundTime}}</font>
				</p>
				<p class="list0 list clear" v-if="key.refundStatus == 2">
					<span class="type2"></span>
					<font style="color:#ff5001;" v-if="key.surplusDay">{{key.surplusDay}}</font>
				</p>
				<p class="list0 list clear" v-if="key.refundStatus == 3">
					<span class="type2"></span>
					<font style="color:#ff5001;" v-if="key.refundInterest">{{key.refundInterest}}</font>
				</p>
				<p class="list0 list clear" v-if="key.refundStatus == 4">
					<span class="type4"></span>
					<font style="color:#ff5001;"></font>
				</p>
				<p class="list1 list clear">
					<span></span>
					<strong>{{key.orderNo}}</strong>
				</p>
				<p class="list2 list clear">
					<span></span>
					<strong>{{key.payAmount}}</strong>
					<i class="success" v-if="key.payStatus"></i>
					<i class="error" v-else="key.payStatus"></i>
				</p>
				<p class="list3 list clear">
					<span></span>
					<strong>{{key.payTime}}</strong>
				</p>
			</article>



		</section>
	</div>
</template>
<script type="text/javascript">
	export default{
		data(){ 
			return{
				item : "100" ,
				index : 1 ,	//
				totalPage : "" ,
				refundStatus:"" ,
				statusHtml:"" ,
				loanList : []
			}
		},
		methods: {
			init(){
				this.getData(1,100) ;
			},
			//
			getData(index,refundStatus){	//
				let _this = this ;
				_this.item = _this.refundStatus = refundStatus ;
				let params={
					refundStatus : _this.refundStatus ,//0123,
					currentPage : index,	//
				}
				_this.$post(_this.$API.GET_LOAN_DATA,params)
				.then((result) => {
					console.log("",result)
					if(result.result == "success"){
						_this.totalPage = result.data.page.totalPage ;		//
						//_this.loanList = result.data.list ;
						_this.loanList = _this.loanList.concat(result.data.list)
					}else{
						_this.$toast(result.msg)
					}
				})
			},
			//
			tabScroll(){
				let _this = this ;
				_this.scroll_ = function () {
					let docHeight = document.body.clientHeight;		//
					let winHeight = window.screen.height ;		//
					let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop ;
					if(scrollTop >= docHeight - winHeight){
						_this.index ++ ;
						if(_this.index <= _this.totalPage){
							_this.getData(_this.index,_this.refundStatus)
						}else{
							_this.$toast("")
						}
					}
				}
					window.addEventListener("scroll",_this.scroll_);
				},
			//
			details(saasId,refundStatus){
				if(refundStatus != 4){
					this.$router.push({
						path:"/recordDetail",
						query: { "objectId": saasId }
					}) ;
				}
			}
		},
		created(){
			this.init() ;
		},
		mounted() {
			this.tabScroll();
		},
		destroyed(){
			window.removeEventListener("scroll",this.scroll_);
		}
	}
</script>
<style lang="scss" scoped type="text/css">
   @import "./index.scss";
</style>


