<template>
	<div id="contain" :style="{height:height+'px'}">
		<header id="header">
			<p class="state clear">
				<span class="green"></span>
				<strong style="color: rgb(115, 234, 128);"></strong>
			</p>
		</header>
		<section id="section">
			<p class="news1 news">
				<span></span>
				<font>{{applyId}}</font>
			</p>
			<p class="news2 news">
				<span></span>
				<font>{{productName}}</font>
			</p>
			<p class="news3 news">
				<span></span>
				<strong>{{amount}}</strong>
				<font></font>
			</p>
			<p class="news4 news">
				<span></span>
				<font>{{new Date().toLocaleDateString()}}</font>
			</p>
			<p class="news7 btn news" v-if="payErr" @click="repay()" ></p>
			<p class="news5 btn news" v-else-if="signWay" @click="online()" ></p>
			<p class="news6 btn news" v-else @click="line()" ></p>
			
		</section>
	</div>
</template>
<script type="text/javascript">
	export default{
		
		data(){
			return{
				height:"" ,
				signWay : true ,
				amount :"" ,
				applyId : "" ,
				productName : "" ,
				payErr : false ,
				redirectUrl : "" ,//
			}
		},
		methods: {
			init(){
				let _this = this ;
				_this.$post(_this.$API.POST_FAST_PAY_RESULT_ZJ)
				.then((result) => {
					console.log(result)
					if(result.result == "success"){
						let obj = result.data ;
						_this.amount = obj.amount ;
						_this.applyId = obj.applyId ;
						let payStatus = obj.payStatus ;
						_this.productName = obj.productName ;
						//
						if(payStatus == "1"){	
							//signWay 1---  2---
							if(obj.signWay == "1"){
								_this.redirectUrl = obj.redirectUrl ;
							}else{
								_this.signWay = false ;
							}
						}else{
							_this.payErr = true ;	
						}
					}else{
						_this.$toast(result.msg)
					}
				})
			},
			//
			repay(){
				this.$router.push({
					path:"/payAgencyFeeZJ",	
				}) ;
			},
			//
			online(){
				console.log("")
				window.location.href = this.redirectUrl
			},
			//
			line(){
				this.$router.push({
					path:"/sign",
					query: {loanId: this.applyId}	
				}) ;
			},
		},
		created () {
			this.height = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight; //
			this.init();
		}
	}
</script>
<style lang="scss" scoped type="text/css">
   @import "./index.scss";
</style>


