<template>
	<div class="contain">
		<header class="header">
			<p class="list1 list clear">{{headerText}}</p>
			<p class="list2 list clear">
				<label id="hopePrice">{{hopePrice}}</label><span></span>
			</p>
			<p class="list3 list clear">
				<span></span>
				<b id="repaymentMode">{{repayText}}</b>
				<b id="rate">{{rate}}</b>
			</p>
		</header>
		<section class="section">
			<article class="news1 news clear">
				<div class="list2 list clear">
					<i></i>
					<span></span>
					<strong id="statusText">{{statusText}}</strong>
					<p class="list1 list clear" id="remark">{{remark}}</p>
				</div>
				<div class="list3 list clear" id="passMoney" v-if="isShow">
					<i></i>
					<span></span>
					<p>
						
						<a id="guaranteeFeePrice">{{guaranteeFeePrice}}</a>
					</p>
					<p style="padding-top: 0.1rem;">
						
						<a id="factoragePrice">{{factoragePrice}}</a>
					</p>
				</div>
			</article>
			<!---->
			<article class="news2 news clear" id="passPay" v-if="isShow">
				<p class="declaration"></p>
				<p class="btn" id="next" @click="next()"></p>
			</article>
			<!---->
			<article class="news2 news clear" id="updateMes" v-if="isModify">
				<p class="declaration"></p>
				<p class="btn" id="toModify" @click="toModify()"></p>
			</article>
			<article class="news3 news clear">
				<p class="list list1 clear">
					<span></span>
					<a href="tel:0571-85890982" style="color: #ff5b00;">0571-85890982</a>
				</p>
				<p class="list list2 clear">
					<span></span>10:00-18:00
				</p>
			</article>
		</section>
	</div>
</template>
<script type="text/javascript">
import API from "@/config/api";
import { MessageBox } from 'mint-ui';
	export default{
		data(){
			return{
				token:"821dd0ea-4133-4338-ac4c-2757bed489ce0",
				headerText:"",
				isShow:false,
				isModify:false,
				loanId:"",	//id
				factoragePrice:"" ,//
				guaranteeFeePrice:"" ,//
				hopePrice:"" ,//
				//repaymentMode:"" ,//
				repayText:"",	//
				statusText:"",	//
				remark:"",	//
				rate:"",	//
			}
		},
		methods: {
				init(){
					let _this = this ;
					let params={
						loanId : _this.loanId ,	
					}
					_this.$post(_this.$API.GET_LOAN_DETAILS_MSG,params)
					.then((result) => {
						console.log("",result)
						if('success' == result.result) {
							let obj = result.data ;
							_this.loanId = obj.id; //
							_this.factoragePrice = obj.factoragePrice == null ? 0 : obj.factoragePrice; //
							_this.guaranteeFeePrice = obj.guaranteeFeePrice == null ? 0 : obj.guaranteeFeePrice; //
							_this.hopePrice = obj.hopePrice == null ? 0 : parseInt(obj.hopePrice.toFixed(2)); //
							let repaymentMode = obj.repaymentMode == null ? "" : obj.repaymentMode; //
							switch (repaymentMode) {
								case "1":
									_this.repayText = "12" ;
									break;
								case "2":
									_this.repayText = "3" ;
									break;
								case "3":
									_this.repayText = "12" ;
									break;
							}
							if(obj.repaymentName != null && obj.repaymentName != '') {
								_this.repayText = obj.repaymentName;
							}
							if(obj.rate&&(obj.rate!=""||obj.rate!=null)){
								_this.rate =""+obj.rate+"%" ;		//
							}
							//
							if(obj.status == 0&&obj.applyStatus == 1){
								_this.statusText = "";
								_this.remark = "~3~";
							}
							//
							if(obj.status == 0&&obj.applyStatus == 3){
								_this.statusText = "";
								_this.remark = "" + (obj.modifyContent == null ? "" : obj.modifyContent);
								_this.isModify = true ;
							}
							//
							if(obj.status == 0&&obj.applyStatus == 4){
								_this.statusText = "";
								_this.remark = "";
							}
							//
							if(obj.status == 1){
								_this.statusText = "";
								_this.remark = "";
							}
							//
							if(obj.status == 2){
								_this.statusText = "";
								_this.remark = "";
							}
							//
							if(obj.status == 3){
								_this.statusText = "";
								_this.remark = ",";
								_this.headerText = "" ;
							}
							//
							if(obj.status == 4&&obj.applyStatus == 6){
								_this.statusText = "";
								_this.remark = "";
								_this.isShow = true ;
								_this.headerText = "" ;
							}
							//
							if(obj.status == 4&&obj.applyStatus == 2){
								_this.statusText = "";
								_this.remark = ",";
							}
							//
							if(obj.status == 4&&obj.applyStatus == 7){
								_this.statusText = "";
								_this.remark = "";
								_this.headerText = "" ;
								if(obj.signWay == "0"){		//
									if(obj.timeStatus){
										_this.$router.push({
											path:"/sign",	//
											query: {loanId: _this.loanId}
										}) ;
									}else{
										_this.$router.push({
											path:"/modifySign",	//
											query: {loanId: _this.loanId}
										}) ;
									}
								}
								if(obj.signWay == "1"){		//
									window.location.href= obj.redirectUrl ;
								}
							}
							//
							if(obj.applyStatus == 8){
								_this.statusText = "";
								_this.remark = "";
							}
							//
							if(obj.applyStatus == 9){
								_this.statusText = "";
								_this.remark = "";
							}
							//
							if(obj.status == 5&&obj.applyStatus == 5){
								_this.$router.push({
									path:"/loanSuccess",	
								}) 
							}
						}else{
							_this.$toast(result.msg)
							 _this.$router.push({
								path:"/loanSuccess",	
							})  
						}
					})
				},
				//
				next(){
					let _this = this ;
					_this.$post(_this.$API.GET_QUERY_PAY_TYPE)
					.then((result) => {
						console.log("",result)
						if('success' == result.result) {
							let data = result.data ;
							let channel = data.channel.payCode ;	//
							if(channel == "104") {		//104
								var zjBank = data.bankList;
								if(zjBank==null||zjBank == ""){
									MessageBox.confirm('').then(action => {
										_this.$router.push({
											path:"/bindcardsZJ",	
										}) ;
									}); 
								}else{
									_this.$router.push({
										path:"/payAgencyFeeZJ",		//
									}) ;
								}
							}else{
								_this.$toast("")
							} 
						}else{
							_this.$toast(result.msg)
						}
					})
					
				},
				//
				toModify(){
					this.$router.push({
						path:"/modifySign",	//
						query: {flag: true}
					}) ;
				}
		},
		created() {
			this.loanId = this.$route.query.loanId
			this.init()
		}

	}
</script>
<style lang="scss" scoped type="text/css">
   @import "./index.scss";
</style>


