<template>
    <div class="clear">
        <div class="modelWrap">
			<div class="wrap"></div>
			<div class="box">
				<div class="modelHeader">
					<h3></h3>
					<span @click="close()">x</span>
				</div>
				<div class="modelBody">
					<p class="text-list" style="font-size: 12px; color: red;"><time>2017710</time></p>
					<p class="text-list"><strong></strong><span>{{creditData.userName}}</span></p>
					<p class="text-list"><strong>()</strong><span>{{creditData.idCard}}</span></p>
					<p class="text-list"><strong>()</strong><span>{{creditData.address}}</span></p>
					<p class="text-list"><strong></strong></p>
					<p class="text-list"><strong></strong>552403</p>
					<p class="text-list"><strong></strong></p>
					<p class="text-list"></p>
					<p class="text-list"><strong> </strong></p>
					<p class="text-list"><b>{{creditData.applyAmount}}</b></p>
					<p class="text-list"><strong></strong></p>
					<p class="text-list"></p>
					<p class="text-list"><strong> </strong></p>
					<p class="text-list">1 </p>
					<p class="text-list">2 </p>
					<p class="text-list"><strong></strong></p>
					<p class="text-list"><span>{{creditData.rate}}</span>%</p>
					<p class="text-list"><span class="openAccountName" style="text-decoration: underline;">{{creditData.openAccountName}}</span></p>
					<p class="text-list"><span class="openAccountBank" style="text-decoration: underline;">{{creditData.openAccountBank}}</span></p>
					<p class="text-list"><span class="openAccount" style="text-decoration: underline;">{{creditData.openAccount}}</span></p>
					<p class="text-list"><strong></strong></p>
					<p class="text-list">1 </p>
					<p class="text-list">2 </p>
					<p class="text-list">3 </p>
					<p class="text-list">4 </p>
					<p class="text-list"><strong></strong></p>
					<p class="text-list">1 </p>
					<p class="text-list">2</p>
					<p class="text-list">3 </p>
					<p class="text-list">4 </p>
					<p class="text-list"><strong></strong></p>
					<p class="text-list">1  1 </p>
					<p class="text-list">2 </p>
					<p class="text-list">32</p>
					<p class="text-list">4</p>
					<p class="text-list"><strong></strong></p>
					<p class="text-list">1 </p>
					<p class="text-list">2 </p>
					<p class="text-list">3 </p>
					<p class="text-list">4</p>
					<div class="clear">
						<p class="text-list" style="width: 49%; float: left;">
							<span></span>
							<font>{{creditData.userName}}</font>
						</p>
						<p class="text-list" style="width: 49%; float: left;">
							<span></span>
							<font></font>
						</p>
						<p class="text-list" style="width: 49%; float: left;">
							<span></span>
							<font class="rq">{{new Date().toLocaleDateString()}}</font>
						</p>
						<p class="text-list" style="width: 49%; float: left;">
							<span></span>
							<font class="rq">{{new Date().toLocaleDateString()}}</font>
						</p>
					</div>
				</div>
				<div class="modelFooter">
					<button @click="close()"></button>
				</div>
					
			</div>
		</div>
    </div>
</template>
<script>

export default {
    data(){
        return{
			isShow:false,
        }
	},
	props: {
			"creditData":{
				type:Object,		//Array,Boolean,Number
				default:false
			}
	    },
    methods:{
        close(){
           this.$emit( "isShow",false) ;
        },
        init(){
			let data = new Date();
  			data.setTime(data.getTime());
  			this.time = data.getFullYear()+"-" + (data.getMonth()+1) + "-" + data.getDate();
		},
    },
    created(){
        this.init() ;
    }
}
</script>
<style lang="scss" scoped type="text/css">
   .modelWrap {
    .wrap {
        position: fixed;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background: #000000;
        z-index: 11111;
        opacity: 0.7;
    }
    .box {
        position: absolute;
        top: 100px;
        left: 5%;
        padding: 0 2.5%;
        width: 85%;
        border-radius: 5PX;
        opacity: 1;
        z-index: 99999;
        background: #ffffff;
        margin-bottom: 100px;
        .modelHeader {
			position: relative;
            text-align: center;
            padding: 12PX 24PX;
            border-bottom: 2PX solid #e5e5e5;
            span {
				position: absolute ;
				right:10PX;
				top:12PX ;
				width: 20PX ;
				height: 20PX ;
				line-height: 20PX;
                font-size: 20PX;
                color: #bbb;
			}
			 h3 {
                text-align: center;
                color: #000000;
                font-size: 24px;
            }
        }
        .modelBody {
            padding: 20PX 0;
            border-bottom: 2PX solid #e5e5e5;
           
            p {
                font-size: 14px;
                color: #000333;
                padding: 8px 0;
            }
        }
        .modelFooter {
            padding: 10PX 24PX;
            text-align: right;
            button {
                color: #ffffff;
                background-color: #337ab7;
                border-color: #2e6da4;
                padding: 4PX 8PX;
                border-radius: 5PX;
            }
        }
    }
}
</style>




