<template>
	<div class="contain">
		<header class="header"></header>
		<section class="section">
			<article class="news clear" v-for="(item,index) in pageData" :key="index">
				<p class="title" v-html="item.question" @click="handleFold(index)"></p>
				<div class="content" v-html="item.answer" v-show="item.show"></div>
			</article> 
		</section>
	</div>	
</template> 
<script type="text/javascript">
let pageData=[
    {
        question:"",
        answer:",.",
        show:false
    },
    {
        question:"?",
        answer:"550.SAAS,.,200.",
        show:false
    },
    {
        question:"?",
        answer:"5.",
        show:false
    },
    {
        question:"?",
        answer:",,.",
        show:false
    },
    {
        question:"?",
        answer:",,.,.",
        show:false
    },
    {
        question:"?",
        answer:",. ",
        show:false
    },
    {
        question:",?",
        answer:",,,.",
        show:false
    },
    {
        question:"?",
        answer:",,,.",
        show:false
    },
    {
        question:"??",
        answer:"201761.5%",
        show:false
    },
    {
        question:"?",
        answer:".",
        show:false
    },
    {
        question:",?",
        answer:",,<br /> <a href='tel:0571-85890982' style='color: #ff5b00;'>0571-85890982</a>",
        show:false
    },
    {
        question:"?",
        answer:",,.",
        show:false
    },
    {
        question:"?",
        answer:",<br /> <a href='tel:0571-85890982' style='color: #ff5b00;'>0571-85890982</a><br />:10:00-18:00",
        show:false
    },
]
export default{
    data(){
        return{
            pageData
        }
    },
    methods: {
        handleFold(index){
            this.pageData.map((v,i)=>{
                if(i==index){
                    v.show=!v.show;
                }
            })
        }
    }
}
</script>
<style lang="scss" scoped type="text/css"> 
      @import "./index.scss";
</style>


