<template>
    <div class="clear">
        <div class="modelWrap">
			<div class="wrap"></div>
			<div class="box">
				<div class="modelHeader">
					<span @click="close()">x</span>
				</div>
				<div class="modelBody">
					<h3></h3>
					<p><span>{{name}}</span> </p>
					<p><span>{{idCard}}</span> </p>
					<p><span>{{time}}</span></p>
					<p></p>
					<p></p>
					<p>1.//////</p>
					<p>1</p>
					<p>2</p>
					<p>3</p>
					<p>4</p>
					<p>2.//</p>
					<p>3.//</p>
					<p>4.</p>
					<p>11123</p>
					<p>214</p>
					<p></p>
					<p></p>
					<p></p>
					<p></p>
					<p> </p>
					<p></p>
				</div>
				<div class="modelFooter">
					<button @click="close()"></button>
				</div>
					
			</div>
		</div>
    </div>
</template>
<script>

export default {
    data(){
        return{
			isShow:false,
        }
	},
	props: {
			"name":{
				type:String,		//Array,Boolean,Number
				default:false
			},
			"idCard":{
				type:String,		//Array,Boolean,Number
				default:false
			}
	    },
    methods:{
        close(){
           this.$emit( "isShow",false) ;
        },
        init(){
			let data = new Date();
  			data.setTime(data.getTime());
  			this.time = data.getFullYear()+"-" + (data.getMonth()+1) + "-" + data.getDate();
		},
    },
    created(){
        this.init() ;
    }
}
</script>
<style lang="scss" scoped type="text/css">
   @import "../../../../static/credit.scss";
</style>




