<template>
	<div id="contain">
		<table border="0" cellspacing="0" cellpadding="">
			<tr>
				<th>{{getData.applySection}}</th>
				<th style="border-left: 1px solid #DDDDDD;">{{getData.applySuccess}}</th>
				<th id="btn"><span @click="isLoan(getData.applyFlag)">+</span></th>
			</tr>
			<tr>
				<td>()</td>
				<td style="border-left: 1px solid #DDDDDD;">()</td>
				<td></td>
			</tr>
		</table>
		<div id="saasList">
			<mt-radio
				v-model="shopValue"
				:options="shopList">
			</mt-radio>
			<div class="btnWrap">
				<p class="btn no" @click="pre()"></p>
				<p class="btn confirm" @click="next()"></p>
			</div>
		</div>
		<section>
			<aside id="concernsFlag" v-if="!concernsFlag">
				<article>
					<h5></h5>
					<div>
						<font style="font-weight: 600; color: #FF5001;"></font>
					</div>
					<p>
						<textarea id="copy">https://mp.weixin.qq.com/mp/profile_ext?action=home&__biz=MzI3NzU4NTk1Ng==#wechat_redirect</textarea>
						<button @click="copy()"></button>
					</p>
				</article>
			</aside>
			<div class="wrap" v-for="(item,index) in getData.loanList">
				<div class="detail">
					<article class="news news1 clear" style="margin-bottom:0.5rem">
						<p class="list list1 clear">
							<span></span>
							<strong class="status" style="color:#ff5001;">{{item.applyStateName}}</strong>
							<a class="store right" style = "float:right;">{{item.storeName}}</a>
						</p>
						<p class="list list2 clear" style="text-align: left;">{{item.productName}}</p>
						<div v-if = "item.approveAccount">
							<p class="list list3 clear" style="text-align: center;"></p>
							<p class="list list4 clear" style="text-align: center;">{{item.approveAccount}}</p>
						</div>
						<div v-else>
							<p class="list list3 clear" style="text-align: center;"></p>
							<p class="list list4 clear" style="text-align: center;">{{item.applyAmount}}</p>
						</div>
					</article>
					<article class="news news2 clear" style="border-bottom: 1px dashed #e1e6ea;padding-bottom:0.2rem">
						<p class="list list1 clear">
							<span></span>
							<a>{{item.productName}}</a>
						</p>
						<p class="list list2 clear">
							<span></span>
							<a>{{item.applyId}}</a>
						</p>
					</article>
				</div>
				<article class="news news3 clear" style="border-bottom: 2PX solid #e1e6ea; margin-buttom = 10px">
					<p class="list list1 clear">
						<span @click="details(item.id,item.status,item.operationStatus,item.refundType)" class="detail" style="padding:0 0 0 10%;"></span>
						<a class="right">&gt;</a>
						<strong v-if="item.loanFlag" @click="changeLoan()" id="lending1" class="lending" style="padding-left: 35%; color:#ff5001;"></strong>		
					</p>
				</article>
			</div>
		</section>
		

	</div>
</template>
<script type="text/javascript">
	import API from "@/config/api";
	import { Toast } from 'mint-ui';
	import { MessageBox } from 'mint-ui';
	import { Radio } from 'mint-ui';
	let getData = {
			
	};
	export default{
		data(){
			return{
				getData ,
				shopValue :"",
				shopList:[],
				concernsFlag : true
			}
		},
		methods: {
			init(){
				let _this = this ;
				_this.$fetch(_this.$API.GET_SUCCESS_MSG)
				.then((result) => {
					if('success' == result.result) {
						console.log(result)
						let obj = result.data
						for(var i = 0 ; i < obj.loanList.length ; i++){
							let status = obj.loanList[i].status ;
							let operationStatus = obj.loanList[i].operationStatus ;
							let statusText = "" ;
							//
							if(status == 0&&operationStatus == 1){
								statusText = "";
							}
							//
							if(status == 0&&operationStatus == 3){
								statusText = "";
							}
							//
							if(status == 0&&operationStatus == 4){
								statusText = "";
							}
							//
							if(status == 1){
								statusText = "";
							}
							//
							if(status == 2){
								statusText = "";
							}
							//
							if(status == 3){
								statusText = "";
							}
							//
							if(status == 4&&operationStatus == 6){
								statusText = "";
							}
							//
							if(status == 4&&operationStatus == 7){
								statusText = "";
							}
							//
							if(operationStatus == 8){
								statusText = "";
							}
							//
							if(operationStatus == 9){
								statusText = "";
							}
							//
							if(status == 5&&operationStatus == 5){
								statusText = "";
							}
							//
							if(status == 5&&operationStatus == 10){
								statusText = "";
							}
							obj.loanList[i].applyStateName = statusText ;
						}
						_this.concernsFlag = obj.concernsFlag ;
						_this.getData = obj ;
					}else{
							_this.$toast(result.msg)
						}
					
					
				})
			},
			copy(){
				console.log("")
				var url = document.getElementById("copy");
					console.log(url.select())
					document.execCommand("copy");
					Toast('');
			},
			isLoan(key){
				let _this = this ;
				let applyFlag = _this.getData.applyFlag ;
				if(applyFlag){
					_this.$fetch(_this.$API.GET_SUCCESS_LOAN)
					.then((result) => {
						console.log(result)	
						if(result.result == "success"){
							let shopList = result.data ;
							if(shopList.length > 1){
								_this.$toast("")
								/* for(let i = 0 ; i < shopList.length ; i ++){
									let item = {
										label: saasList[i].shopName ,
										value: saasList[i].saasId
									}
									_this.shopList.push(item)
								} */
							}else{
								_this.$router.push({
									path:"/loan",	
								}) ;
							}
						}else{
							_this.$toast(result.msg)
						}
					})	
				}else{
					MessageBox.confirm(',').then(action => {
						_this.$router.push({
							path:"/shopList",	
						}) ;
					},() => {
							//
						});
				} 
				
			},
			changeLoan(){
				MessageBox.confirm('','?').then(action => {
					Toast('');
				},() => {
							//
						});
			},
			details(id,a,b,c){
				console.log(a,b,c)
				let _this = this ;
				if((a == 5&&b == 5)||(a == 5&&b == 10)){
					switch(c){
						case 1 : 
								_this.$router.push({	//
									path:"/RepayYinHang",
									query: { "loanId": id }	
								}) ;
								break ;
						case 2 : 
								_this.$router.push({	//
									path:"/RepayHanHua",
									query: { "loanId": id }	
								}) ;
								break ;
					}
				}else{
					_this.$router.push({
						path:"/loanDetail",
						query: { "loanId": id }	
					}) ;
				}
				
			}
        },
		created () {
			this.init()
		}

	}
</script>
<style lang="scss" scoped type="text/css">
   @import "./index.scss";
</style>


