import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/views/index'             //
import Authorize from "@/views/authorize/index"         //
import Register from "@/views/register/index"         //
import Login from '@/views/login/index'         //
import ForgetPassword from '@/views/modify/forgetPassword/index'  //
import ModifyPassword from '@/views/modify/modifyPassword/index'  //  
import ModifyPhone from '@/views/modify/modifyPhone/index'  // 
import BasicInfo from '@/views/info/basicInfo/index'  //
import ContactInfo from '@/views/info/contactInfo/index'  //
import DetaileInfo from '@/views/info/detaileInfo/index'  //
import ShopInfo from '@/views/info/shopInfo/index'  //
import Loan from '@/views/loan/loan/index'  //
import LoanDetail from '@/views/loan/loanDetail/index'  //
import LoanSuccess from '@/views/loan/loanSuccess/index'  //
import PayAgencyFeeZJ from '@/views/agencyFee/zhongjin/payAgencyFeeZJ/index'  //()
import PayAgencyResultZJ from "@/views/agencyFee/zhongjin/payAgencyResultZJ/index" //()
import Sign from '@/views/agencyFee/sign/index'  //
import ModifySign from "@/views/agencyFee/modifySign/index"  //
import RepayYinHang from '@/views/repay/yinHang/index'  //
import RepayHanHua from '@/views/repay/hanHua/index'  //
import Find from '@/views/find/index'  //
import User from '@/views/user/index'  //
import MyAccount from '@/views/user/myAccount/index'  //
import ShopList from '@/views/user/shopList/index'  //
import BankCards from '@/views/user/bankCards/index'  //
import BindCardsZJ from "@/views/user/bankCards/bindcardsZJ/index" //
import Record from '@/views/user/record/index'  //
import RecordDetail from "@/views/user/record/recordDetail/index"//
import Help from '@/views/user/help/index'  //
import About from '@/views/user/about/index'  //
import Weixin from '@/views/weixin/index'  //


Vue.use(Router)
const routes = [
  {
    path: "/",
    component: Authorize,
    name: "",
    meta: {
      title: "",
      isFooter: false
    }
  },
  {
    path: "/authorize",
    component: Authorize,
    name: "",
    meta: {
      title: "",
      isFooter: false
    }
  },
  {
    path: "/register",
    component: Register,
    name: "",
    meta: {
      title: "",
      isFooter: false
    }
  },
  {
    path: "/index",
    component: Index,
    name: "",
    meta: {
      title: "",
      isFooter: true,
      item: 1
    }
  },
  {
    path: "/login",
    name: "",
    component: Login,
    meta: {
      title: "",
      isFooter: false
    }
  },
  {
    path: "/forgetPassword",
    name: "",
    component: ForgetPassword,
    meta: {
      title: "",
      isFooter: false
    }
  },
  {
    path: "/modifyPassword",
    name: "",
    component: ModifyPassword,
    meta: {
      title: "",
      isFooter: true,
      item: 4
    }
  },
  {
    path: "/modifyPhone",
    name: "",
    component: ModifyPhone,
    meta: {
      title: "",
      isFooter: true,
      item: 4
    }
  },
  {
    path: "/basicInfo",
    name: "",
    component: BasicInfo,
    meta: {
      title: "",
      isFooter: false
    }
  },
  {
    path: "/contactInfo",
    name: "",
    component: ContactInfo,
    meta: {
      title: "",
      isFooter: false
    }
  },
  {
    path: "/detaileInfo",
    name: "",
    component: DetaileInfo,
    meta: {
      title: "",
      isFooter: false
    }
  },
  {
    path: "/shopInfo",
    name: "",
    component: ShopInfo,
    meta: {
      title: "",
      isFooter: false
    }
  },
  {
    path: "/loan",
    name: "",
    component: Loan,
    meta: {
      title: "",
      isFooter: false,
      item: 2
    }
  },
  {
    path: "/loanDetail",
    name: "",
    component: LoanDetail,
    meta: {
      title: "",
      isFooter: true,
      item: 2
    }
  },
  {
    path: "/loanSuccess",
    name: "",
    component: LoanSuccess,
    meta: {
      title: "",
      isFooter: true,
      item: 2
    }
  },
  {
    path: "/payAgencyFeeZJ",
    name: "",
    component: PayAgencyFeeZJ,
    meta: {
      title: "",
      isFooter: true,
      item: 2
    }
  },
  {
    path: "/payAgencyResultZJ",
    name: "",
    component: PayAgencyResultZJ,
    meta: {
      title: "",
      isFooter: true,
      item: 2
    }
  },
  {
    path: "/sign",
    name: "",
    component: Sign,
    meta: {
      title: "",
      isFooter: true,
      item: 2
    }
  },
  {
    path: "/modifySign",
    name: "",
    component: ModifySign,
    meta: {
      title: "",
      isFooter: true,
      item: 2
    }
  },
  {
    //
    path: "/repayYinHang",
    name: "",
    component: RepayYinHang,
    meta: {
      title: "",
      isFooter: true,
      item: 2
    }
  },
  {
    //
    path: "/repayHanHua",
    name: "",
    component: RepayHanHua,
    meta: {
      title: "",
      isFooter: true,
      item: 2
    }
  },
  {
    path: "/find",
    name: "",
    component: Find,
    meta: {
      title: "",
      isFooter: true,
      item: 3
    }
  },
  {
    path: "/user",
    name: "",
    component: User,
    meta: {
      title: "",
      isFooter: true,
      item: 4
    }
  },
  {
    path: "/myAccount",
    name: "",
    component: MyAccount,
    meta: {
      title: "",
      isFooter: true,
      item: 4
    }
  },
  {
    path: "/shopList",
    name: "",
    component: ShopList,
    meta: {
      title: "",
      isFooter: true,
      item: 4
    }
  },
  {
    path: "/bankCards",
    name: "",
    component: BankCards,
    meta: {
      title: "",
      isFooter: true,
      item: 4
    }
  },
  {
    path: "/BindCardsZJ",
    name: "",
    component: BindCardsZJ,
    meta: {
      title: "",
      isFooter: true,
      item: 4
    }
  },
  {
    path: "/record",
    name: "",
    component: Record,
    meta: {
      title: "",
      isFooter: true,
      item: 2
    }
  },
  {
    path: "/recordDetail",
    name: "",
    component: RecordDetail,
    meta: {
      title: "",
      isFooter: true,
      item: 2
    }
  },
  {
    path: "/help",
    name: "",
    component: Help,
    meta: {
      title: "",
      isFooter: false
    }
  },
  {
    path: "/about",
    name: "",
    component: About,
    meta: {
      title: "",
      isFooter: false
    }
  },
  {
    path: "/weixin",
    name: "",
    component: Weixin,
    meta: {
      title: "",
      isFooter: false
    }
  }
];

export default new Router({
  mode: "history",
  routes
});



