<template>
	<div class="contain" :style="{height:height+'px'}">
		<header class="header">
			<p></p>	
		</header>
		<section class="section">
			<article class="first news">
				<h3 class="title"></h3>
				<p class="list list1 clear">
					<span><font></font></span>
					<input type="text" v-model="dataMsg.firstLinkMan" placeholder="" />
				</p>
				<p class="list list2 clear">
					<span><font></font></span>
					<select v-model="dataMsg.firstRelation">
						<option value=""></option>
						<option value=""></option>
						<option value=""></option>
						<option value=""></option>
					</select>
				</p>
				<p class="list list3 clear" style="border: none;">
					<span><font></font></span>
					<input type="text" v-model="dataMsg.firstLinkManMobile" placeholder="" onkeyup="value=value.replace(/[^\d]/g,'')" />
				</p>
			</article>
			<article class="second news">
				<h3 class="title"></h3>
				<p class="list list1 clear">
					<span><font></font></span>
					<input type="text" v-model="dataMsg.secondLinkMan" placeholder="" />
				</p>
				<p class="list list2 clear">
					<span><font></font></span>
					<select v-model="dataMsg.secondRelation">
						<option value=""></option>
						<option value=""></option>
						<option value=""></option>
						<option value=""></option>
						<option value=""></option>
					</select>
				</p>
				<p class="list list3 clear" style="border: none;">
					<span><font id="smobile"></font></span>
					<input type="text" v-model="dataMsg.secondLinkManMobile" placeholder="" onkeyup="value=value.replace(/[^\d]/g,'')" />
				</p>
			</article>
		</section>
		<footer class="footer">
			<p class="btn" @click="submit()"></p>
		</footer>
	</div>	
</template>
<script type="text/javascript">
	let dataMsg = {
		direction:"" ,
		firstLinkMan : "" ,	//
		firstRelation : "" , //
		firstLinkManMobile : "" , //
		secondLinkMan : "" ,
		secondRelation : "" ,
		secondLinkManMobile : "",
		step : 4 
	}
	export default{
		data(){
			return{
				height:"",
				dataMsg ,
			}
		},
		methods: {
			//
			init(){
				let _this = this ;
				let params={
						step : 4 
					}
				_this.$post(_this.$API.GET_CONTACT_MSG,params)
				.then((result) => {
					if(result.result == "success"){
						_this.dataMsg.firstLinkMan = result.data.firstLinkMan ;
						_this.dataMsg.firstRelation = result.data.firstRelation ;
						_this.dataMsg.firstLinkManMobile = result.data.firstLinkManMobile ;
						_this.dataMsg.secondLinkMan = result.data.secondLinkMan ;
						_this.dataMsg.secondRelation = result.data.secondRelation ;
						_this.dataMsg.secondLinkManMobile = result.data.secondLinkManMobile ;
					}else{
						_this.$toast(result.msg) ;
					}
					
				})
			},
			submit(){
				let _this = this ;
				if(_this.dataMsg.firstLinkMan == ""||_this.dataMsg.firstLinkMan == null){
					_this.$toast("") ;
					return false ;
				}
				if(_this.dataMsg.firstRelation == ""||_this.dataMsg.firstRelation == ""){
					_this.$toast("") ;
					return false ;
				}
				if(!_this.$fn.checkModile(_this.dataMsg.firstLinkManMobile)){
					_this.$toast('')
					return false ;
				}
				if(_this.dataMsg.secondLinkMan == ""||_this.dataMsg.secondLinkMan == null){
					_this.$toast("") ;
					return false ;
				}
				if(_this.dataMsg.secondRelation == ""||_this.dataMsg.secondRelation == ""){
					_this.$toast("") ;
					return false ;
				}
				if(!_this.$fn.checkModile(_this.dataMsg.secondLinkManMobile)){
					_this.$toast('')
					return false ;
				}
				let params = _this.dataMsg ;
				_this.$post(_this.$API.POST_CONTACT_MSG_SUBMIT,params)
				.then((result) => {
					if(result.result == "success"){
						if(_this.$route.query.direction){	//
							_this.$router.push({
								path:"/shopInfo",	//
								query: { 
									"flag": _this.$route.query.flag ,
									"direction": true 
								}
							}) ;
						}else{
							_this.$router.push({
								path:"/myAccount",	//
							}) ;
						}
					}else{
						_this.$toast(result.msg) ;
					}
				})
			}
        },
		created () {
			this.height = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight; //
			this.init() ;
		}
	}
</script>
<style lang="scss" scoped type="text/css">
   @import "./index.scss";
</style>


