<template>
	<div class="contain" :style="{height:height+'px'}">
		<article class="news1 news clear"  v-show="isShow == 1">
			<p class="notice"></p>
			<div class="shopList">
				<p class="list1 list clear" v-for="key in shopList" @click="shopDetails(key.saasId,key.shopNo)">
					<font>{{key.shopName}}</font>	
					<b style="color: #CCCCCC;float: right;">></b>
				</p>
			</div>	
			<p class="relative" @click="addShop()">+&nbsp;</p>
		</article>

		<article class="news2 news clear" v-show="isShow == 2">
			<p class="notice"></p>
			<div class="saasList">
				<mt-radio
					v-model="prodectValue"
					:options="prodectList">
				</mt-radio>
			</div>
			<div class="btnWrap">
				<p class="btn" @click="pre()"></p>
				<p class="btn" @click="next()"></p>
			</div>
		</article>
		<article class="news3 news clear" v-show="isShow == 3">
			<p class="notice">SaaS</p>
			<div class="saasList">
				<mt-radio
					v-model="SaaSValue"
					:options="SaaSList">
				</mt-radio>
			</div>
			<div class="btnWrap">
				<p class="confirm" @click="confirm()"></p>
			</div>
		</article>
	</div>
</template>
<script type="text/javascript">
	import { Radio } from 'mint-ui';
	export default{
		components:{
			
		},
		data(){
			return{
				height:"" ,
				isShow:1 ,
				shopList:[] ,
				prodectValue :"",
				prodectList:[
					{
						label:'',
						value:'1'
					},
					{
						label:'',
						value:'2'
					}
				],
				SaaSValue :"",
				SaaSList:[],

			}
		},
		
		methods: {
			//
			init(){
				let _this = this ;
				_this.$fetch(_this.$API.GET_SHOP_LIST) 
				.then((result) => {
					console.log(result)
					if(result.result == "success"){
						_this.shopList = result.data ;
					}else{
						_this.$toast(result.msg) ;
					}
				})
			},
			//
			shopDetails(saasId,shopNo){
				console.log(saasId) ;
				console.log(shopNo) ;
				this.$router.push({
					path:"/shopInfo",	//
					query: { 
						"saasId": saasId ,
						"shopNo": shopNo
					}
				}) ;
			},
			//
			addShop(){
				this.isShow = 2 ;	
			},
			//
			pre(){
				this.isShow = 1 ;	
			},
			//
			next(){
				let _this = this ;
				if( _this.prodectValue ){
					let params={
							type : _this.prodectValue 
						}
					_this.$post(_this.$API.GET_SAAS_LIST,params)
					.then((result) => {
						if(result.result == "success"){
							this.isShow = 3 ;
							let saasList = result.data.saasList ;
							for(let i = 0 ; i < saasList.length ; i ++){
								let item = {
									label: saasList[i].saasName ,
									value: saasList[i].saasId
								}
								_this.SaaSList.push(item)
							}
							console.log(_this.SaaSList)
						}else{
							_this.$toast(result.msg) ;
						}
					})
				}else{
					_this.$toast("") ;
				}
			},
			//SAAS
			confirm(){
				let _this = this ;
				if(_this.SaaSValue){
					let params={
						saasId : _this.SaaSValue
					}
					_this.$post(_this.$API.POST_SELECT_SAAS,params)
					.then((result) => {
						console.log("")
						console.log(result)
						if(result.result == "success"){
							if(!result.data.isSkip){
								_this.$toast("") ;
							}else{
								window.location.href = result.data.url ;								
							}
						}else{
							_this.$toast(result.msg) ;
						}
					})
				}else{
					_this.$toast("SaaS") ;
				}
				
			}
		},
		created () {
			this.init() ;
			this.height = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight; //
		}
	}
</script>
<style lang="scss" scoped type="text/css">
   @import "./index.scss";
</style>


