<template>
	<div class="contain">
		<header class="header"></header>
		<section class="section">
			<article class="news1 news clear" >
				&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Saas
				
			</article>
			<article class="news2 news clear" >
				<p><strong></strong></p>
				<p>552403</p>
			</article>
			<article class="news3 news clear" >
				<div class="Ditumap">  
					<div id="BaiduDitu"></div>
				</div>
				<div class="panel panel-default">
					<div id="allmap"></div>
				</div>
			</article>
		</section>
	</div>
</template>
<script type="text/javascript">
	export default{
		data(){
			return{
								
			}
		},
		methods:{
			//
				initMap(){
				    this.createMap();//
				    this.setMapEvent();//
				},
				 createMap(){
				    var map = new BMap.Map("BaiduDitu");//
				    map.centerAndZoom('',11);//
				    window.map = map;//map
				},
				//
				 setMapEvent(){
				    map.enableDragging();//()
				    map.enableScrollWheelZoom();//
				    map.enableDoubleClickZoom();//()
				    map.enableKeyboard();//
				},
		},
		mounted(){
			this.initMap();//
			 
		},
	}

</script>
<style lang="scss" scoped type="text/css">
   @import "./index.scss";
</style>


